

.project-item .main-links {
  border-bottom: none;
  text-decoration: none;
}

.project-item .image {
  border-radius: 6px;
  background-size: cover;
  background-position: center center;
  width: 100%;
  padding-bottom: 75%;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  box-shadow: 0px 6px 12px -5px rgba(50, 50, 50, 0.5);
  -webkit-transition: background 0.35s ease-in-out;
  -moz-transition: background 0.35s ease-in-out;
  -o-transition: background 0.35s ease-in-out;
  transition: background 0.35s ease-in-out;
}

.bg-yellow { background-color: #ffc; }
.bg-peach { background-color: #fcc; }
.bg-green { background-color: #cfc; }
.bg-purple { background-color: #ccf; }

.project-item:hover .image,
.main-links:focus .image {
  -webkit-transform: translateY(-0.25rem);
          transform: translateY(-0.25rem);
}

.project-item .title {
  font-size: 1rem;
  margin: 0.6em 0;
}

.title {
  color: $text-color;
}

.columns {
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
}

.columns .column-item {
  vertical-align: top;
  width: calc(100% - 8px);
  -ms-flex-preferred-size: calc(100% - 8px);
      flex-basis: calc(100% - 8px);
  margin: 8px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.home li::before {
  content: 'â†’';
  font-size: 0.8em;
  margin-right: 0.75em;
}

.archive-entry .date {
  margin-left: 0.75em;
  font-size: 0.85em;
}

.selected {
  background-color: darken($bg-color, 5%) !important;
}

.filter {
  position: fixed;
  background: #e3e3e3;
  padding: 6px;
  border-radius: 6px;
  cursor: default;
  top: -2.5rem;
  transition: top .5s;
}

.filter-on {
  top: 1rem;
}

.hidden {
  display: none;
  visibility: none;
  opacity: 0;
}

@media only screen and (min-width: 440px) {
  .columns {
    flex-direction: row;
  }

  .columns .column-item {
    width: calc(50% - 16px);
    -ms-flex-basis: calc(50% - 16px);
        -ms-flex-preferred-size: calc(50% - 16px);
        flex-basis: calc(50% - 16px);
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }
}

@media only screen and (min-width: 880px) {
  .columns .column-item {
    width: calc((100% / 3) - 16px);
    -ms-flex-basis: calc((100% / 3) - 16px);
        -ms-flex-preferred-size: calc((100% / 3) - 16px);
        flex-basis: calc((100% / 3) - 16px);
    -ms-flex-negative: 0;
        flex-shrink: 0;
  }

}

@media only screen and (max-width: 880px) {
  header ul {
    padding-left: 0;
  }
}

@media (prefers-reduced-motion: reduce) {
  .project-item:hover .image,
  .main-links:focus .image {
    -webkit-transform: none;
            transform: none;
  }
}


@-webkit-keyframes colorcycle {
  0% {
    color: $blue;
  }
  50% {
    color: $pink;
  }
  100% {
    color: $blue;
  }
}

@keyframes colorcycle {
  0% {
    color: $blue;
  }
  50% {
    color: $pink;
  }
  100% {
    color: $blue;
  }
}